import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class NormalizationService {
  constructor() {}

  // Excerpted from https://github.com/ikr/normalize-for-search/blob/master/src/normalize.js added 
  normalizeForSearch(s: string): string {
    function filter(c: string) {
      switch (c) {
        case '':
        case '':
          return 'ae';

        case '':
          return 'aa';

        case '':
        case '':
        case '':
        case '':
          return 'a';

        case '':
        case '':
          return 'c';

        case '':
        case '':
        case '':
        case '':
        case '':
          return 'e';

        case '':
        case '':
        case '':
          return 'i';

        case '':
        case '':
          return 'oe';

        case '':
          return 'n';

        case '':
        case '':
        case '':
          return 'o';

        case '':
        case '':
          return 's';

        case '':
          return 'ue';

        case '':
        case '':
        case '':
          return 'u';

        case '':
          return 'ss';

        case '':
          return '';

        default:
          return c;
      }
    }

    let normalized = '';
    let i: number, l: number;
    s = s.toLowerCase();
    for (i = 0, l = s.length; i < l; i = i + 1) {
      normalized = normalized + filter(s.charAt(i));
    }
    return normalized;
  }
}
