import { ModalController } from '@ionic/angular';
import { Component, OnInit, Input } from '@angular/core';

// class ClipboardItem {
//   constructor(items: any, options: any = null) { }
// }
declare class ClipboardItem {
  constructor(data: { [mimeType: string]: Blob });
}
@Component({
  selector: 'app-share',
  templateUrl: './share.page.html',
  styleUrls: ['./share.page.scss'],
})
export class SharePage implements OnInit {
  @Input() canvas: HTMLCanvasElement;
  constructor(public modalController: ModalController) { }

  ngOnInit() {
  }

  close() {
    this.modalController.dismiss({
      result: 'cancel'
    });
  }

  copyToClipboard(event) {
    try {
      const canvas = this.canvas;
      this.canvas.toBlob(function (blob) {
        if (navigator['clipboard']) {
          // Safe to use Async Clipboard API!
          const clip = navigator['clipboard'] as any;
          clip.write([new ClipboardItem({ 'image/png': blob })]).then(function () {
            console.log('Copied to clipboard successfully!');
          }, function (err) {
            console.error(err);
            console.error('Unable to write to clipboard. :-(');
          });
        } else {
          const img = document.createElement('img');
          img.src = canvas.toDataURL();
          document.body.appendChild(img);
          const r = document.createRange();
          r.setStartBefore(img);
          r.setEndAfter(img);
          r.selectNode(img);
          const sel = window.getSelection();
          sel.addRange(r);
          const wascopied = document.execCommand('Copy');
          if (!wascopied) {
            alert('You need to right click or long press on image to copy it.');
          }
          img.remove();
        }
      });
    } catch (error) {
      console.error(error);
    }
  }

  // copyImage(canvas) {
  //   debugger;
  //   const img = document.createElement('img');
  //   img.src = canvas.toDataURL();
  //   document.body.appendChild(img);
  //   const r = document.createRange();
  //   r.setStartBefore(img);
  //   r.setEndAfter(img);
  //   r.selectNode(img);
  //   const sel = window.getSelection();
  //   sel.addRange(r);
  //   document.execCommand('Copy');
  // }

}
